











import {
	mapActions,
	mapState
} from 'vuex'
export default {
	data() {
		return {

		}
	},
	computed: {
		...mapState('Base', ['Rank']),
	},
	methods: {
		...mapActions('Base', ['CommonFetch']),
	},
	async onLoad() {
		await this.CommonFetch({
			url: '/store/v0/ad/ranks',
			name: 'Rank'
		})
	}
}
